import React from 'react';
import Paper from '@material-ui/core/Paper';
import Card from '@material-ui/core/Card';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';



const useStyles = makeStyles({
    box: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        width: 180,
        height: 130,

    },
    mt20: {
        marginTop: 20
    },
    m: {
        marginTop: 10,
        marginLeft: 5,
        textAlign: 'center'
    }

});

function App(props) {
    const classes = useStyles();

    return (
        <>
            <Paper elevation={3} className={classes.box}>{props.svg}
                <div>
                    <Typography component="h3" variant="h6" color="textPrimary" gutterBottom className={classes.m} >
                        {props.title}
                    </Typography>
                    <Typography component="h3" variant="h6" color="textPrimary" className={classes.mt20} gutterBottom>
                        {props.count}
                    </Typography>
                </div>
            </Paper>
        </>
    );
}

export default App;