import React, { useContext, useEffect } from 'react';
import { Context } from '../../Context';

function App() {
    const { state, dispatch } = useContext(Context);

    useEffect(() => {
        dispatch({
            type: 'setNowLocal',
            value: '设备管理'
        });
    }, []);

    return (
        <>
            设备管理页面
        </>
    );
}

export default App;