import React, { useContext, useEffect, useState } from 'react';
import { Context } from '../../../Context';
import MaterialTable from 'material-table';



import {
    columnsArr,
    optionsSetting,
    localizationConfig,
    tableIcons
} from './materialTableConfig'


function App() {
    const { state, dispatch } = useContext(Context);

    const [userList, setUserList] = useState();
    async function getUserList() {
        const res = await fetch(`${state.host}/userList?currentPage=1&pageSize=5`, {
            credentials: 'include', method: 'get', headers: {
                "Content-type": "application/json"
            },
        });
        const result = await res.json();
        let pageSize = result.data.total;
        const res2 = await fetch(`${state.host}/userList?pageSize=${pageSize}`, {
            credentials: 'include', method: 'get', headers: {
                "Content-type": "application/json"
            },
        });
        const result2 = await res2.json();
        let data = result2.data.records;
        setUserList(data);
        console.log(data);
    }
    useEffect(() => {
        dispatch({
            type: 'setNowLocal',
            value: '用户管理 - 普通用户'
        });


        getUserList();

    }, []);


    

    return (
        <MaterialTable
            title="用户列表"
            columns={columnsArr}
            data={userList}
            icons={tableIcons}
            options={optionsSetting}
            // 显示设置
            localization={localizationConfig}
            editable={{
                onRowAdd: newData =>
                    new Promise(resolve => {
                        setTimeout(() => {
                            resolve();
                            setUserList(prevState => {
                                // const data = [...prevState.data];
                                // data.push(newData);
                                // return { ...prevState, data };
                            });
                        }, 600);
                    }),
                onRowUpdate: (newData, oldData) =>
                    new Promise(resolve => {
                        setTimeout(() => {
                            resolve();
                            if (oldData) {
                                setUserList(prevState => {
                                    const data = [...prevState.data];
                                    data[data.indexOf(oldData)] = newData;
                                    return { ...prevState, data };
                                });
                            }
                        }, 600);
                    }),
                onRowDelete: oldData =>
                    new Promise(resolve => {
                        setTimeout(() => {
                            resolve();

                            alert('你没有权限');
                            console.log(oldData);

                        }, 600);
                    }),
            }}
            options={{
                actionsColumnIndex: -1
            }}
        />
    );
}

export default App;