import React ,{useContext,useEffect,useState}from 'react';
import Title from './Title';
import PersonBlock from '../../component/PersonBlock'
import { makeStyles } from '@material-ui/core/styles';
import { findByLabelText } from '@testing-library/react';
import Grid from '@material-ui/core/Grid';
import PersonIcon from '@material-ui/icons/Person';
import GroupIcon from '@material-ui/icons/Group';
import PersonAddIcon from '@material-ui/icons/PersonAdd';
import { Context } from '../../Context';
import { Link } from 'react-router-dom';
import moreuser from './svg/多用户模式.svg';
import todayuser from './svg/用户.svg'
import newuser from './svg/新增用户.svg'
import warn from './svg/异常.svg'

const useStyles = makeStyles(theme => ({
    root: {
        // display:'flex',
        flexGrow: 1,
        marginTop:15,
        // justifyContent: 'space-between'
    },
    perIcon: {
        margin: 5,
        fontSize: 80
    },

    flex:{
        display:'flex',
        justifyContent: 'center',
        
    }
}));

function App(props) {
    const { state, dispatch } = useContext(Context);

    const[data, setData] = useState([0,0,0,0]);
    async function getUserCount() {
        const res = await fetch(`${state.host}/getUserCount`, { credentials: 'include', method: 'get' });
        const result = await res.json();
        const data = result.data;
        // setUserList(ul);
        setData(data);
        console.log(data);
    }


    const classes = useStyles();

    useEffect(() => {
        getUserCount();

    }, []);
    return (
        <>
            <Title>数据统计</Title>
            <Grid container className={classes.root} spacing={1} >
                <Grid item xs={3} className={classes.flex} >
                <Link to='/home/user' style={{ textDecoration: 'none',
  color: 'black' }} >   <PersonBlock title='用户总量' count={data[0]}  icon={( <img src={moreuser} alt="moreuser" />)} /></Link>
                </Grid>
                <Grid item xs={3} className={classes.flex}>
                    <PersonBlock title='今日访问量'count={data[1]}   icon={( <img src={todayuser} alt="todayuser" />)} />
                </Grid>
                <Grid item xs={3} className={classes.flex}>
                <Link to='/home/user'style={{ textDecoration: 'none',
  color: 'black' }} >   <PersonBlock  title='今日新增用户'  count={data[2]}  icon={( <img src={newuser} alt="newuser" />)}/></Link>

                </Grid>
                <Grid item xs={3} className={classes.flex}>
                    <PersonBlock  title='用户异常数据量'  count={data[3]}  icon={( <img src={warn} alt="warn" />)}/>

                </Grid>
            </Grid>



        </>
    );
}

export default App;