import React from 'react';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import ListSubheader from '@material-ui/core/ListSubheader';
import DashboardIcon from '@material-ui/icons/Dashboard';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import PeopleIcon from '@material-ui/icons/People';
import BarChartIcon from '@material-ui/icons/BarChart';
import LayersIcon from '@material-ui/icons/Layers';
import AssignmentIcon from '@material-ui/icons/Assignment';
import { Link } from 'react-router-dom';
import List from '@material-ui/core/List';
import Collapse from '@material-ui/core/Collapse';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import DraftsIcon from '@material-ui/icons/Drafts';
import SendIcon from '@material-ui/icons/Send';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import StarBorder from '@material-ui/icons/StarBorder';
import { makeStyles } from '@material-ui/core/styles';
import SupervisorAccountIcon from '@material-ui/icons/SupervisorAccount';
const style = {
  textDecoration: 'none',
  color: 'white'
}
const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,

  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
}));

export default function ListItems() {

  const classes = useStyles();
  const [userOpen, setUserOpen] = React.useState(true);

  const handleClick = () => {
    setUserOpen(!userOpen);
  };


  return (
    <List>
      <div>
        <ListItem button>
          <ListItemIcon>
            <DashboardIcon style={{ color: 'white' }} />
          </ListItemIcon>
          <Link to='/home/' style={style}> <ListItemText primary="仪表盘" /></Link>
        </ListItem>
        <ListItem button>
          <ListItemIcon >
            <ShoppingCartIcon style={{ color: 'white' }} />
          </ListItemIcon>
          <Link to='/home/device' style={style}> <ListItemText primary="设备管理" /></Link>
        </ListItem>
        <ListItem button onClick={handleClick}>
          <ListItemIcon>
            <PeopleIcon style={{ color: 'white' }} />
          </ListItemIcon>
          {/* <Link to='/home/user' style={style}> <ListItemText primary="用户管理" /></Link> */}
          <ListItemText primary="用户管理" />
          {userOpen ? <ExpandLess /> : <ExpandMore />}
        </ListItem>
        <Collapse in={userOpen} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            <Link to='/home/user' style={style}>
              <ListItem button className={classes.nested}>
                <ListItemIcon>
                <PeopleIcon style={{ color: 'white' }} />
                </ListItemIcon>
                <ListItemText primary="普通用户" />
              </ListItem>
            </Link>
            <Link to='/home/admin' style={style}>
              <ListItem button className={classes.nested}>
                <ListItemIcon>
                <SupervisorAccountIcon style={{ color: 'white' }} />
                </ListItemIcon>
                <ListItemText primary="管理员" />
              </ListItem>
            </Link>
          </List>
        </Collapse>

        <ListItem button>
          <ListItemIcon>
            <BarChartIcon style={{ color: 'white' }} />
          </ListItemIcon>
          <ListItemText primary="报表" />
        </ListItem>
        <ListItem button>
          <ListItemIcon>
            <LayersIcon style={{ color: 'white' }} />
          </ListItemIcon>
          <ListItemText primary="机构管理" />
        </ListItem>


      </div>

    </List>

  );
}

