export default function reducer(state, action) {
    switch (action.type) {
        case 'setArrange':
            return {
                ...state,
                arrange:action.value
            }
        case 'robot':
            return {
                ...state,
                robotState:action.value
            }  
            case 'messList':
            return {
                ...state,
                messList:action.value
            }
            case 'placeList':
            return {
                ...state,
                placeList:action.value
            }
            case 'login':
                return {
                    ...state,
                    user:action.value
                }
                case 'setNowLocal':
                return {
                    ...state,
                    nowLocal:action.value
                }
    }

}