import React from 'react';
import {
  G2,
  Chart,
  Geom,
  Axis,
  Tooltip,
  Coord,
  Label,
  Legend,
  View,
  Guide,
  Shape,
  Facet,
  Util
} from "bizcharts";

const data = [
  {
    date: "2020-03-14",
    data: 0
  },
  {
    date: "2020-03-15",
    data: 0
  },
  {
    date: "2020-03-16",
    data: 7
  },
  {
    date: "2020-03-17",
    data: 0
  },
  {
    date: "2020-03-18",
    data: 5
  },
  {
    date: "2020-03-19",
    data: 2
  },
  {
    date: "2020-03-20",
    data: 0
  }
];
const cols = {
  data: {
    
    alias:'人数' // 别名
  },
  date: {
    
    alias:'日期' // 别名
  },
};
function App(props) {
  return (
    <> <Chart height={400} data={data} scale={cols} forceFit>

      <Axis name="date"  title line={{
        stroke: 'dddddd',
        fill: '#ffffff',
        lineWidth: 1
      }} />
      <Axis name="data"  title line={{
        stroke: 'dddddd',
        fill: '#ffffff',
        lineWidth: 1
      }} />
      <Tooltip
        crosshairs={{
          type: "y"
        }}
      />
      <Geom type="line" position="date*data" size={3} />
      <Geom
        type="point"
        position="date*data"
        size={4}
        shape={"circle"}
        style={{
          stroke: "#fff",
          lineWidth: 1
        }}
      />
    </Chart></>
  );
}

export default App;